


















































































































@import './../../style/custom.styl'

.city-container
    background: #fff;
    border-top: 1px solid $ddd;
    border-bottom: 1px solid $ddd;
    margin-bottom: 10px;

h3 
    margin: 0;
    padding: 0 10px;
    font-size: 16px;
    font-weight: 400;
    line-height: 46px;

.city-box 
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;

.city-box li 
    box-sizing: border-box;
    flex: 0 0 25%;
    text-align: center;
    line-height: 40px;
    border-top: 1px solid #eee;
    border-left: 1px solid #eee;

.city-box li:first-child
    border-left: 0;

.city-box li:nth-child(5n)
    border-left: 0;

