





















































































































































































@import './../../style/custom.styl'

.login-form
    margin: 10px 0;

.login-form .cell-box
    padding: 0;

.login-form .cell-item-right
    padding: 0 15px;

.code-box
    display: flex;
    padding: 0 15px;

.code-img
    flex: none;
    width: 80px;
    height: 30px;
    margin-right: 15px;

.code-btn-box
    flex: auto;
    font-size: 12px;
    color: $blank;

.blue-color
    margin-top: 4px;
    color: $blue;

.login-tips
    padding: 12px 15px;
    text-align: left;
    line-height: 24px;
    font-size: 14px;
    color: #f85367;

.change-password-btn    
    display: inline-block;
    font-size: 14px;
    color: $blue;
    line-height: 20px;
