











































































































































@import './../../style/custom.styl'

.search-box
    margin: 10px 0;
    border-top: 1px solid $ddd;
    border-bottom: 1px solid $ddd;
    
.address-list
    margin-bottom: 10px;
    border-bottom: 1px solid $ddd;

.list-item
    border-top: 1px solid $ddd;
    padding: 10px 15px;
    text-align: left;
    background: #fff;

.item-name
    font-size: 16px;
    color: $blank;
    line-height: 24px;

.item-address
    margin-top: 3px;
    font-size: 14px;
    color: $999;
    line-height: 18px;

.ellipsis
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
